<template>
    <div class="you-like">
      <i class="iconfont">&#xe635;</i>
    </div>
</template>

<script>
  export default {
    name: 'you-like'
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import "../../common/stylus/iconfont.styl"
  .you-like
    font-size 0.875rem
    color #ff757e
    text-align center
    position relative
    &:before,&:after
      content ''
      display block
      position absolute
      width 38%
      top 50%
      left 0
      height 1px
      background-color #ff757e
    &:after
      left inherit
      right 0

</style>



