import CONFIG from './config'
import axios from 'axios'
import qs from 'qs'


/*
* 
* */
export function itemAddressList(opts) {
  return axios.post(CONFIG.API.ADDRESS_LIST).then(res => res.data)
}

/*
* 
* */
export function itemAddressAdd(opts) {
  return axios.post(CONFIG.API.ADDRESS_ADD, qs.stringify({
    isDefault :  opts.isDefault,
    provinceId : opts.provinceId,
    cityId :     opts.cityId,
    districtId : opts.districtId,
    townId :     opts.townId,
    address :    opts.address,
    mobile :     opts.mobile,
    realname :   opts.realname
  })).then(res => res.data)
}

//
export function itemAddressEdit(opts) {
  return axios.post(CONFIG.API.ADDRESS_EDIT, qs.stringify({
    addressId :  opts.addressId,
    isDefault :  opts.isDefault,
    provinceId : opts.provinceId,
    cityId :     opts.cityId,
    districtId : opts.districtId,
    townId :     opts.townId,
    address :    opts.address,
    mobile :     opts.mobile,
    realname :   opts.realname
  })).then(res => res.data)
}


//
export function itemAddressQuery(opts) {
  return axios.post(CONFIG.API.ADDRESS_QUERY_USER, qs.stringify({
    addressId :  opts.addressId
  })).then(res => res.data)
}

//
export function itemAddressDelete(opts) {
  return axios.post(CONFIG.API.ADDRESS_DELETE, qs.stringify({
    addressId :  opts.addressId
  })).then(res => res.data)
}


