<template>
  <div @click="toDetail" class="goods-item">
    <img class="goods-img" @load="loadImage" :src="data.cover" alt="">
    <div class="wrapper">
      <p class="goods-name" style="-webkit-box-orient:vertical">{{data.title}}</p>
      <p class="current-price">{{priceTxt(data.minPayTb,data.cashPrice)}}</p>
      <p class="original-cos">{{data.oldPrice}}</p>
    </div>
  </div>
</template>

<script>
  import tool from '../../common/js/util'
  export default {
    name: 'goods-item',
    props: {
      data: {
        type: Object,
        default: null
      }
    },
    methods:{
      priceTxt(tb,rmb){
        return tool.priceStr(tb,rmb)
      },
      toDetail(){
        console.log('sdasdasdas')
        this.$emit('toDetail',this.data.id)
      },
      loadImage(){
        this.$emit('loadImage')
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  .goods-item
    display flex
    flex-direction column
    justify-content space-between
    width 100%
    background-color #fff
    .goods-img
      width 100%
    .wrapper
      .goods-name
        overflow hidden
        display -webkit-box
        text-overflow ellipsis
        -webkit-line-clamp 2
        /*! autoprefixer: off */
        -webkit-box-orient:vertical;
        /*! autoprefixer: on */
        padding 0.5rem 0.5rem 0 0.5rem
        font-size 0.875rem
        line-height 1.188rem
        color #212121
      .current-price
        padding 0.5rem
        padding-bottom 0.2rem
        color #ff4800
        font-size 0.875rem
        line-height 1.125rem
      .original-cos
        padding 0 0.5rem
        font-size 0.688rem
        color #999
        padding-bottom 0.594rem
        text-decoration line-through
</style>



