<template>
  <div v-if="showHeader" :style="lineStyle" class="address-header">
    <div class="go-back" v-if="showGoBack" @click="historyBack"></div>
    <h1 class="title">{{title}}</h1>
    <div v-if="goEdit" @click="addAddress" class="address-option"></div>
    <div v-else @click="saveAddress" class="address-option"></div>
  </div>
</template>

<script>
  export default {
    name: 'address-header',
    data(){
      return {
        showHeader : false
      }
    },
    created(){
      let ua = navigator.userAgent.toLowerCase();
      let isWeixin = ua.indexOf('micromessenger') != -1;

      if (!isWeixin) {
        this.showHeader=true
        // alert('Dragondean');
      }else{
        this.showHeader=true
      }
    },
    props:{
      title:{
        type:String,
        default:''
      },
      showGoBack:{
        type:Boolean,
        default:true
      },
      lineStyle:{
        type:String,
        default:''
      },
      goEdit: {
        type:Boolean,
        default:true
      }
    },
    methods: {
      //
      addAddress(e){
        //console.log('')
        this.$router.push({path:'/addressAdd'})
      },
      //
      saveAddress(e){
        this.$emit("goSave");
      },
      historyBack(){
        this.$emit("historyBack")
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .address-header
    width 100%
    height 2.75rem
    position relative
  .go-back
    width 2.75rem
    height 100%
    position absolute
    &:before
      content ''
      position absolute
      width 0.8rem
      height 0.8rem
      border 2px solid #000
      border-width 2px 0 0 2px
      transform rotate(315deg)
      top 0.9rem;
      left 1rem
  .title
    text-align center
    line-height 2.75rem
    font-size 1.125rem
  .address-option
    position absolute
    right 1rem
    top 0rem
    height 2.75rem
    line-height 2.75rem
    font-size 0.875rem
    color rgb(66,176,233)
    cursor pointer
</style>



